import React from "react";
const youtube_icon = require("../../Assets/youtube_icon.svg");
const youtube_banner = require("../../Assets/Capas/Capa YT.png");
const public_child = require("../../Assets/Icons/Publico - Infantil.png");
const money = require("../../Assets/Icons/Money.png");
const partners = require("../../Assets/Icons/Parceiros.png");

export default function YoutubeWin({ name }) {
  return (
    <div className="container go-up">
      <div className="game-content bigger">
        <div className="platform-win">
          <h3>{name}, sua plataforma ideal é o</h3>
          <div>
            <p className="title">Youtube</p>
            <img
              src={youtube_icon}
              height="200px"
              width="200px"
              className="ml-3"
              alt="banner"
            />
          </div>
          <p className="mt-4 justify-text">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim
            ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut
            aliquip ex ea commodo consequat. Duis aute irure dolor in
            reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla
            pariatur. Excepteur sint occaecat cupidatat non proident, sunt in
            culpa qui officia deserunt mollit anim id est laborum.
          </p>
          <div>
            <p className="title mt-4">
              Por que o Youtube é a melhor plataforma para você
            </p>
            <div className="info-content">
              <div className="info-container">
                <p className="info-title">Monetização</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={money}
                  alt="banner"
                />
                <p className="info-text">
                  A monetização da plataforma se da por se associar
                  gratuitamente ao YouTube Partner Program(YPP), ou Programa de
                  Parcerias do Youtube.
                </p>
              </div>
              <div className="info-container">
                <p className="info-title">Público alvo</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={public_child}
                  alt="banner"
                />
                <p className="info-text">
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                </p>
              </div>
              <div className="info-container">
                <p className="info-title">Parcerias</p>
                <img
                  style={{
                    width: 150,
                    height: 150
                  }}
                  src={partners}
                  alt="banner"
                />
                <p className="info-text">
                  Você precisa ter pelo menos 1 mil inscritos e ter, no mínimo,
                  4 mil horas de conteúdo assistido nos últimos 12 meses para se
                  tornar parceiro do Youtube.
                </p>
              </div>
            </div>
          </div>
          <div>
            <p className="title mt-4">Uma informação importante</p>
            <p>
              Tendo as duas primeiras regras básicas, você pode entrar com o
              pedido para inscrever seu canal no método de monetização. <br />
              <br />O canal que quer entrar no programa é analisado pelo número
              de inscritos, engajamento e comportamento no canal (curtidas nos
              vídeos, comentários e compartilhamentos), tipo do conteúdo, além
              de outros critérios como produção de conteúdo original, ter
              frequência regular de postagens, estar localizado em um dos países
              válidos pelo programa e obedecer às regras da comunidade, seguir
              todas as regras e diretrizes de conteúdo da plataforma; Ter um
              conteúdo adequado (não ofensivo) para os anunciantes, possuir os
              direitos autorais do conteúdo postado e ter uma conta do Google
              AdSense vinculada.
            </p>
            <p className="info-title">Pagamento</p>
            <p>
              O pagamento no YouTube é em dólares e feito a partir da regra de
              CPM (custo por mil), ou seja, a cada 1000 views, um youtuber pode
              ganhar valores entre 0,25 e 4,50 dólares.
            </p>
            <p className="info-title">
              Por que as pessoas escolheram essa plataforma
            </p>
            <div>
              <img className="banner" src={youtube_banner} alt="banner" />
            </div>
            <p className="mt-4 justify-text">
              Quer levar discussões LGBT+ ao cenário RPG, como forma de
              inclusão. Vídeos curtos para serem mais acessíveis e intuitivos,
              planeja conversar com pessoas de fora da comunidade. Tem boa
              frequência de postagem, algumas vezes vídeos rápidos, menores que
              3 mins. Ficou em dúvida entre Facebook Watch e Youtube, mas por
              conta da duração dos vídeos, escolheu o Youtube.
            </p>
          </div>
          <div>
            <p className="info-title mt-4">Fontes/Referências</p>
            <p className="justify-text fonts">
              <i>https://klickpages.com.br/blog/monetizacao-no-youtube/</i>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
